body
  background-color: var(--color-black)
  background-image: url(data:image/png;base64,)

h1
  @media (min-width: 769px)
    font: bold 5.5rem/6rem var(--main-font)
  @media (max-width: 768px) and (min-width: 576px)
    font: bold 4rem/4.5rem var(--main-font)
  @media (max-width: 575px)
    font: bold 3rem/3.5rem var(--main-font)
h2
  @media (min-width: 576px)
    font: bold 2.3rem/1.8rem var(--second-font)
  @media (max-width: 575px)
    font: bold 1.5rem/2rem var(--second-font)
h3
  font: bold 2.5rem/3rem var(--main-font)

p, li, a
  @media (min-width: 769px)
    font: normal 1.8rem/2.6rem var(--main-font)
  @media (max-width: 768px)
    font: normal 1.6rem/2.4rem var(--main-font)

.inner
  width: 100%
  max-width: 90rem
  margin-left: auto
  margin-right: auto
  @media (max-width: 1599px) and (min-width: 375px)
    padding-left: 2rem
    padding-right: 2rem
  @media (max-width: 374px)
    padding-left: 1rem
    padding-right: 1rem

.main-wrapper
  max-width: 110rem
  width: 100%
  margin: 1rem auto 3rem
  padding: 5rem 0
  background: linear-gradient(152deg, #0c1e38 0%, #30203d 100%)
  border-top: 1rem solid var(--color-accent)
  @media (max-width: 999px)
    border-bottom: 1rem solid var(--color-accent)

.inline-link
  position: relative
  display: inline-block
  padding: 0 .2rem
  border: .1rem solid transparent
  transition: all .3s
  @media (min-width: 576px)
    &::after
      content: ""
      position: absolute
      bottom: 0
      left: 0
      right: 0
      height: .1rem
      transform-origin: right bottom
      transform: scaleX(0)
      transition: transform .3s ease 0s
    &:hover:not(:focus-visible)::after
      transform-origin: left bottom
      transform: scaleX(1)
    &:not(&--white)::after
      background-color: var(--color-accent)
    &--white::after
      background-color: var(--color-white)

  &:not(&--white)
    color: var(--color-accent)
    &:focus-visible
      border: .1rem solid var(--color-accent)
      &:hover
        background-color: var(--color-accent)
        color: var(--color-black)

  &--white
    color: var(--color-white)
    &:focus-visible
      border: .1rem solid var(--color-white)
      &:hover
        background-color: var(--color-white)
        color: var(--color-black)
